<template>
  <div class="staticReports">
    <el-container>
      <el-header>
        <div class="staticReports loadingtext minWidth"
            v-loading="imgLoading"
            element-loading-text="加载中..."
            element-loading-background="rgba(255, 255, 255, 0.8)">
	        <div class="taticReports-content">
            <el-row type="flex" class="staticReports-header" justify="space-between">
            	<el-col :span="17" class="titleMT"><span class="mz" @click="$router.back(-1)">流程报表</span><i :class="icon"></i>
		          </el-col>
                <el-col :span="6" class="titleBtn clearfix">
                  <el-button round @click="refresh" type="primary"  size="mini">重置</el-button>
                  <el-button round @click="search" type="primary"  size="mini">搜索</el-button>
                </el-col>
		        </el-row>
		        <br>
		        <!--<hr>-->
		          <div class="statement reportCondition">
                 <span class="fz16">动态-岗位/人-以“人”统计退回率</span>

                 <!--选人控件input begin-->
                <el-row  class="staticReports-middle lh35  staticReportsMiddle">
                  <el-col :span="2" :sm="4" :md="1" class="processSelect"><span>组织选择</span></el-col>
                  <el-col  :span="14" class="process-inputbox">
                  <i class="iconfont icon-organization" @click="openDialog('project_dialog')" ></i>
                  <el-input @dblclick.native="openDialog('project_dialog')" :disabled="true" v-model="organSelectVal" size="mini" class="el-inputHeigth"></el-input>
                </el-col>
                  <!--<el-col :span="14"><el-input @dblclick.native="openDialog('project_dialog')" :disabled="true" v-model="organSelectVal" size="mini" class="el-inputHeigth"></el-input></el-col>-->
                </el-row>
                <!--选人控件input end-->

                 <!--时间段 begin-->
                <el-row  class="reportCondition-date reportConditionDate">
                  <el-col :span="2" :sm="4" :md="1" class="timeDuan zj"><span>时间段</span></el-col>
                  <el-col :span="7" :sm="8" class="timeSelect">
                    <el-radio-group v-model="organSelectRadio">
                      <el-radio :label="1">本周</el-radio>
                      <el-radio :label="2">本月</el-radio>
                      <el-radio :label="3">本季度</el-radio>
                      <el-radio :label="4">本年</el-radio>
                      <el-radio :label="5">其他</el-radio>
                    </el-radio-group>
                  </el-col>
                  <el-col :span="7" :sm="6" v-if="organSelectRadio === 5 ">
                    <div class="border">
                      <el-date-picker
                        style="width: auto"
                        v-model="model.date"
                        type="daterange"
                        value-format="yyyy-MM-dd"
                        range-separator="~"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                      </el-date-picker>
                    </div>
                  </el-col>
                  <el-col :span="4" class="buttonWrap  buttonWrapTA">
                    <!--<el-button @click="search" type="primary"  size="mini"><i class="el-icon-search"></i>查询</el-button>
                    <el-button @click="refresh" type="primary"  size="mini"><i class="el-icon-refresh"></i>重置</el-button>-->
                    <el-button round @click="exportList" type="primary"  size="mini"><i class="iconfont icon-export"></i>导出</el-button>
                  </el-col>
                </el-row>
                <!--时间段 end-->
              </div>

              <div class="statement">
                <el-table
                  ref="singleTable"
                  :height="isShowReportCondition?fullHeight-270:fullHeight-280"
                  :data="tableData"
                  @sort-change="sortChange"
                  highlight-current-row
                  border
                  style="width: 100%">
                  <el-table-column
                    align="center"
                    property="rowNumber"
                    sortable="custom"
                    width="120"
                     label="排名">
                  </el-table-column>
                  <el-table-column
                    align="left"
                    property="userName"
                    sortable="custom"
                    label="岗位">
                  </el-table-column>
                  <el-table-column
                    align="left"
                    property="department"
                    sortable="custom"
                    label="部门" >
                  </el-table-column>
                  <el-table-column
                  prop="processTotalCnt"
                  sortable='custom'
                  label="处理实例数">
                </el-table-column>
                <el-table-column
                  prop="returnCnt"
                  sortable='custom'
                  label="退回实例数">
                </el-table-column>
                  <el-table-column
                    align="center"
                    property="returnRate"
                    sortable="custom"
                    label="退回率" width="120">
                  </el-table-column>
                  <div slot="empty" v-show="!imgLoading">
                    <p :style="{'marginTop': '23px'}">暂无数据</p>
                  </div>
                </el-table>
              </div>
           </div>
        </div>
      <!--弹窗组件-->
      <component :is="comp.component" v-if="showdialog"
                 @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild" v-bind:childDataUrl="childDataUrl"
                 v-bind:oneCheck="oneCheck"></component>
      </el-header>
    </el-container>
  </div>

</template>

<script>
  import projectdialog from '../components/dailog/project_dialog'
  import moment from 'moment'
  import util from '../js/util'
  import loading from '../plugins/loading/loading';

  /*本周*/
  var firstDay = moment().day(0).format('YYYY-MM-DD');
  var lastDay = moment().day(6).format('YYYY-MM-DD');

  /*本月*/
  var firstMonth = moment().startOf('month').format('YYYY-MM-DD');
  var lastMonth = moment().endOf('month').format('YYYY-MM-DD');

  /*本季度*/

  var firstSeason = moment().startOf('quarter').format('YYYY-MM-DD');
  var lastSeason = moment().endOf('quarter').format('YYYY-MM-DD');

  /*本年*/

  var firstYear = moment().startOf('year').format('YYYY-MM-DD');
  var lastYear = moment().endOf('year').format('YYYY-MM-DD');

  export default {
    name: '',
    components: {
      'project_dialog': projectdialog,
    },
    data() {
      return {
        organSelectVal: '', // 组织选择input 内容
        //动态变更弹出框组件
        comp: {},
        childDataUrl:{url:''},//传给子组件的值
        showdialog: false,
        fullHeight: document.documentElement.clientHeight,
        organSelectRadio: 1, // 以“人”统计退回率组织选择下 单选框
        model: {
          selectForm: {},
          date:''  //选择时间
        },
        oneCheck: false,
        startDate: '',
        endDate: '',
        isShow:true,
        isShowReportCondition: true,
        arrtReportCondition: '收起',
        icon:'el-icon-arrow-down',
        iconIf: 'el-icon-arrow-up',
        tableData: [],
        orgDepId: '', // 组织选择人员id
        orderBy: '', // 排序
        // 加载动画
        imgLoading: false
      }
    },
    mounted(){
      // this.getList();
    },
    methods:{
      change(){
        switch (this.organSelectRadio) {
          case 1:
            this.startDate = firstDay;
            this.endDate = lastDay;
            break;
          case 2:
            this.startDate = firstMonth;
            this.endDate = lastMonth;
            break;
          case 3:
            this.startDate = firstSeason;
            this.endDate = lastSeason;
            break;
          case 4:
            this.startDate = firstYear;
            this.endDate = lastYear;
            break;
          case 5:
            this.startDate = this.model.date[0] ?`${this.model.date[0]} 00:00:00`:'';
            this.endDate = this.model.date[1] ?`${this.model.date[1]} 23:59:59`:'';
            break;
        }
      },
      // 报表导出
      //获取导出列表数据
      getExportDataList(callback) {
        let that = this;
        this.change();
        let obj = {
          startDate: this.startDate, // this.startDate  '2012-01-01'
          endDate: this.endDate, // this.endDate  '2018-06-23'
          orgDepId: this.orgDepId, // this.orgDepId  '50000094'
          orderBy: this.orderBy
        };
        loading.startLoading();
        // 分页信息
        that.$axios.post('/api/StatisticalreturnratePeople/page', obj).then(result => {
          that.exportData = result.data.processesReportList;
          callback();
          loading.endLoading();
        }).catch(err => {
          that.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
          loading.endLoading();
        });
      },

      // 报表导出
      exportList() {
        let that = this;
        this.change();
        loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        let obj = {
          "startDate": this.startDate,
          "endDate": this.endDate,
          "orgDepId": this.orgDepId?parseInt(this.orgDepId):null,
          "orderBy":this.orderBy,
          "filterEmptyComment":"0",
          "userID":""
        };
          that.$axios.post('/api/exportExcelForTable',  {'tableName':'动态-岗位/人-以“人”统计退回率','params':obj}, {responseType: 'blob'}).then((result) => {
            const content = result.data;
            const dowLoadFileName = '动态-岗位/人-以“人”统计退回率';
            util.exportSearchList(dowLoadFileName, content);
            loading.endLoading();
          }).catch((err) => {
            loading.endLoading();
            that.$message.error(`导出Excel失败!ERROR MESSAGE:${err}`);
          });
      },

      // 报表条件 展开/收起
      showToggleReportCondition(){
        this.isShowReportCondition = !this.isShowReportCondition;
        if(this.isShowReportCondition){
          this.arrtReportCondition="收起";
          this.iconIf="el-icon-arrow-up"
        }else {
          this.arrtReportCondition="展开";
          this.iconIf="el-icon-arrow-down"
        }
      },
      // 点击每行数据
      rowClick (row, event, column) {
        if (column.label === '流程图') {
        }
      },

      getList() {
        if (this.orgDepId.length === 0) {
          this.$message.warning('请选择组织进行查询！');
          return;
        }
        // loading.startLoading();
        this.imgLoading = true;
        this.change();
        let obj = {
          startDate: this.startDate, // this.startDate  '2012-01-01'
          endDate: this.endDate, // this.endDate  '2018-06-23'
          orgDepId: this.orgDepId, // this.orgDepId  '50000094'
          orderBy: this.orderBy
        };
        this.$axios.post('/api/StatisticalreturnratePeople/page',obj).then((result) => {
          this.tableData = result.data.statisticalreturnrate;
          for (var i = 0; i < result.data.statisticalreturnrate.length; i++) {
            result.data.statisticalreturnrate[i].returnRate = (parseFloat(result.data.statisticalreturnrate[i].returnRate)) + '%'
          }
          // loading.endLoading();
          this.imgLoading = false;
        }).catch((err) => {
          // loading.endLoading();
          this.imgLoading = false;
        })
      },
      //表格排序
      sortChange(column){
        this.orderBy = `${column.prop} ${column.order.replace('ending','')}`;
        this.getList();
      },
      //-------------------------------      弹窗
      /**
       * 打开弹出框组件
       */
      openDialog(type,url) {
        // 弹出框类型
        this.comp.component = type;
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
        this.oneCheck = true;
        this.childDataUrl.getTree='getTree';
        this.childDataUrl.isFirst='2';
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },

      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        //选择组织架构
        if (this.oneCheck) {
          let txt = data.last.lastNode;
          this.model.companys_t = txt;
          this.model.companys = txt;
          if(this.model.companys.length > 15){
            this.model.companys = this.model.companys.substr(0,20)+'...';
          }
          this.model.selectForm.departmentID = data.item.orgID;
          this.model.selectForm.companyCode = data.last.lastId.toString();
          this.childDataUrl.chooseProject = data;
          this.orgDepId = data.last.lastId;
          this.organSelectVal = txt;
        }
        this.hideComponent();
      },
      // 查询操作
      search(){
        this.getList();
      },
      // 重置操作
      refresh(){
        this.childDataUrl.chooseProject = new Array();
        this.startDate = '';
        this.endDate = '';
        this.organSelectVal = '';
        this.organSelectRadio = 1;
        this.orgDepId = '';
        this.orderBy = '';
      }
    },
    computed: {},
    watch: {
      fullHeight () {
        return document.documentElement.clientHeight;
      }
    },
  }
</script>
<style lang="scss" scoped>
     .staticReports .timeDuan.zj{
           min-width: 72px;
     }
</style>



